function Griditem(props){
    
    
    const classes = useStyles();
    
    return (
        <div className={classes.root}>
          <Grid container spacing={3}
          justify="center"
          >
           
            <Grid item xs={6} sm={2} >
            {/* <Paper className={classes.paper} ><CreateChart chtname="test1" color="#800080" title="Active Cases per 1K" value={1} /> </Paper> */}
            <CreateChart chtname="test1" color="#800080" title="Active Cases per 1K" value={props.data[0].ACTIVE_CASES_PER_1000} /> 
            </Grid>
            <Grid item xs={6} sm={2}>
              {/* <Paper className={classes.paper} ><CreateChart chtname="test2" color="#34a4eb" title="New Cases per 1K" value={0.3}/></Paper> */}
              <CreateChart chtname="test2" color="#34a4eb" title="New Cases per 10K" value={props.data[0].NEW_CASES_PER_10000}/>
            </Grid>
            <Grid item xs={6} sm={2}>
              {/* <Paper className={classes.paper} ><CreateChart chtname="test3" color="#3333cc" title="Deaths per 1K" value={0.03}/></Paper> */}
              <CreateChart chtname="test3" color="#3333cc" title="Deaths per 1K" value={props.data[0].DEATHS_PER_1000}/>
            </Grid>
            
          </Grid>
        </div>
      );
}

function CreateChart(props) {
  const chart = useRef(null);
  
  useLayoutEffect(() => {
   
    var capacity = 1000;
    var value = props.value;
    var circleSize = 0.8;
    console.log(props.value)
    var component = am4core.create(props.chtname, am4core.Container)
    component.width = am4core.percent(100);
    component.height = am4core.percent(100);
    component.logo.height =  -15000; 
    var chartContainer = component.createChild(am4core.Container);
    chartContainer.x = am4core.percent(50)
    chartContainer.y = am4core.percent(50)
    
    var circle = chartContainer.createChild(am4core.Circle);
    circle.fill = am4core.color("#f8f9fa");
    
    var circleMask = chartContainer.createChild(am4core.Circle);
    
    var waves = chartContainer.createChild(am4core.WavedRectangle);
    waves.fill = am4core.color(props.color);
    waves.mask = circleMask;
    waves.horizontalCenter = "middle";
    waves.waveHeight = 10;
    waves.waveLength = 30;
    waves.y = 500;
    circleMask.y = -500;
    
    component.events.on("maxsizechanged", function(){
      var smallerSize = Math.min(component.pixelWidth, component.pixelHeight);  
      var radius = smallerSize * circleSize / 2;
    
      circle.radius = radius;
      circleMask.radius = radius;
      waves.height = smallerSize;
      waves.width = Math.max(component.pixelWidth, component.pixelHeight);  
    
      // capacityLabel.y = radius;
    
      var labelRadius = radius + 15
    
      capacityLabel.path = am4core.path.moveTo({x:-labelRadius, y:0}) + am4core.path.arcToPoint({x:labelRadius, y:0}, labelRadius, labelRadius);
      capacityLabel.locationOnPath = 0.5;
    
      setValue(value);
    })
    
    
    function setValue(value){
       var y = - circle.radius - waves.waveHeight + (1 - value / capacity) * circle.pixelRadius * 2;
       waves.animate([{property:"y", to:y}, {property:"waveHeight", to:10, from:15}, {property:"x", from:-50, to:0}], 5000, am4core.ease.elasticOut);
       circleMask.animate([{property:"y", to:-y},{property:"x", from:50, to:0}], 5000, am4core.ease.elasticOut);
    }
    
    
    var label = chartContainer.createChild(am4core.Label)
    var formattedValue = component.numberFormatter.format(value, "#.#a");
    formattedValue = formattedValue.toUpperCase();
    
    label.text = value ;
    label.fill = am4core.color("#03a9f4");
    label.fontSize = 30;
    label.horizontalCenter = "middle";
    
    
    var capacityLabel = chartContainer.createChild(am4core.Label)
    
    var formattedCapacity = component.numberFormatter.format(capacity, "#.#a").toUpperCase();;
    console.log(props.title)
    capacityLabel.text = props.title;
    capacityLabel.fill = am4core.color("#1769aa");
    capacityLabel.fontSize = 20;
    capacityLabel.textAlign = "middle";
    capacityLabel.padding(0,0,0,0);
    
    chart.current = component;
    return () => {
        component.dispose();
    };
  }, [props.data,props.chtname]);

  return (
   
  <div id={props.chtname}    
  //  style={{ width: "100%", height: "250" }}
  >
    </div>
  );
}

 const  KPIViews = () => {
    const { loading, error, data } = useQuery(data2);
    const classes = useStyles();

    
    if (loading) {
      return <div className='loader_graph'>Loading...</div>;
    }
    if (error) {
      console.error( error);
      return  <div>Error</div>;
    }
    return <div style={{textAlign:'center'}}><br/>
  <Card className={classes.root} >
 <CardHeader 
 className={classes.header}
  // style={{Color: "#000", Background: "blue"}}
        
  action={
          
          <Informationbutton header="How it works?" 
          body=
          "Provides holistic view of cases per Thousand or 10 Thousands, which helps us to infer to the population" 
          header1="How it is calculated?" 
          body1=
          " The Viral Load module provides three measures namely by applying mathematical calculation at 5 day rolling average Active COVID cases per 1000 people, New COVID cases per 10000 people and Death cases per 1000 people. 
            Main source for data is JHU data repository"
          className={classes.action}/>
        }

        
        title="Cases Summary"
        subheader="For Telengana"

        
      />
 {/* <Informationbutton header="test" body="test"/> */}
 
 <Divider />
    <CardContent style={{ width: "100%", height: "350px" }}> 
    <Griditem  data={data.latest_cases_per_thousand}/>
    <SmallBars />
    </CardContent>
    <CardActions>
        <Button size="small" color="primary">
          <a href="https://github.com/CSSEGISandData/COVID-19/tree/master/csse_covid_19_data/csse_covid_19_daily_reports" target="_blank" style={{textDecoration:"none"}}>Source: Johns Hopkins - Viral load information</a>
          </Button>
        
      </CardActions>
    </Card>
    
     {/* <div><Griditem  data={data.latest_cases_per_thousand}/> */}
     {/* <Informationbutton header="test" body={"this is an body \n explainning the details"}/> */}
     {/* <IconButton color="primary" aria-label="Info" onClick={()=>handleOpen}>
    <InfoIcon/>
    </IconButton> */}
    
    </div>
  };


  
 

export default KPIViews;
export {Griditem}