import Typography from '@material-ui/core/Typography';
import Link from '@material-ui/core/Link';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import Divider from '@material-ui/core/Divider';
// import CardMedia from '@material-ui/core/CardMedia';
import { red } from '@material-ui/core/colors';
import CardContent from '@material-ui/core/CardContent';
import { makeStyles } from '@material-ui/core/styles';
import Informationbutton from './Informationbutton'
import Ms from './Ms'
const useStyles = makeStyles((theme) => ({
    root: {
      maxWidth: '100%',
      flexGrow: 1,
      textAlign:'left'
    },header:{
        background: 'linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%)',
        borderRadius: 3,
        border: 0,
        color: 'white',
        height: 48,
        padding: '0 40px',
        boxShadow: '0 3px 5px 2px rgba(255, 105, 135, .3)',
      },
      headermain:{
        background: 'linear-gradient(45deg, #2196f3 30%, #21cbf3 90%)',
        borderRadius: 3,
        border: 0,
        color: 'white',
        height: 48,
        padding: '0 40px',
        boxShadow: '0 3px 5px 2px rgba(255, 105, 135, .3)',


      },
    media: {
      height: 0,
      paddingTop: '56.25%', // 16:9
    },
    expand: {
      transform: 'rotate(0deg)',
      marginLeft: 'auto',
      transition: theme.transitions.create('transform', {
        duration: theme.transitions.duration.shortest,
      }),
    },
    expandOpen: {
      transform: 'rotate(180deg)',
    },
    avatar: {
      backgroundColor: red[500],
    },
    paper: {
        padding: theme.spacing(0),
        textAlign: 'center',
        color: theme.palette.text.secondary,
        margin: 'auto',
        
      },
      action: {
        backgroundColor: '#ffff',
        // foregroundColor:'#ffffff'
        // textAlign:"right",
        // alignItems: 'right',
        // flexGrow:1
        // padding: '21px',
      }
  }));

export default function Page1() {
const classes = useStyles();
    return (
<div>
    <br/><br/>
    <Card className={classes.root}>

    <CardHeader className={classes.headermain}
            title={"Mobility Analytics"}
            subheader="For Telengana"
            action={
              <Informationbutton 
              
              header="testa" body="testa" 
              header="How it works?" 
            body=
            " These Community Mobility Reports aim to provide insights into what has changed in response to policies aimed at combating COVID-19.
             The reports chart movement trends over time by geography, across different categories of places such as retail and recreation, groceries and pharmacies, parks, transit stations, workplaces, and residential."
            header1="How it is calculated?"
            body1=
            " Changes for each day are compared to a baseline value for that day of the week.
             The baseline is the median value, for the corresponding day of the week, during the five week period 3 Jan  6 Feb 2020.
             The reports show trends over several weeks with the most recent data representing approximately 23 days ago (this is how long it takes to produce the reports)." 
              
              
              className={classes.action}/>
            }
     />
    <Divider />
    </Card>
    <Ms placeRender="Details"/>
</div>
    );
}
