import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Modal from '@material-ui/core/Modal';
import Backdrop from '@material-ui/core/Backdrop';
import Fade from '@material-ui/core/Fade';
import InfoIcon from '@material-ui/icons/Info';
import IconButton from '@material-ui/core/IconButton';
import { Typography } from "@material-ui/core";
import TextField from '@material-ui/core/TextField';
const useStyles = makeStyles((theme) => ({
  modal: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  },
  papermodel: {
    backgroundColor: theme.palette.background.paper,
    border: '2px solid #000',
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
  },
  root:{
    padding:'20px',
  }
}));
export default function Informationbutton(props) {
    const classes = useStyles();
    const [open, setOpen] = React.useState(false);
  
    const handleOpen = () => {
      setOpen(true);
    };
  
    const handleClose = () => {
      setOpen(false);
    };
  
    return (
      <div>


<IconButton color="inherit" aria-label="Info" onClick={handleOpen} style={{padding: '20px'}}>
    <InfoIcon/>
    </IconButton>
        <Modal
          aria-labelledby="transition-modal-title"
          aria-describedby="transition-modal-description"
          className={classes.modal}
          open={open}
          onClose={handleClose}
          closeAfterTransition
          BackdropComponent={Backdrop}
          BackdropProps={{
            timeout: 500,
          }}
        >
          <Fade in={open}>
            <div className={classes.papermodel}>
              
              <h3 id="transition-modal-title">{props.header}</h3>
                <TextField id="outlined-basic" label="" variant="outlined" style={{width: '100ch',}}
                multiline
                value={props.body}
                />
               <h3 id="transition-modal-title">{props.header1}</h3> 
                <TextField id="outlined-basic" label="" variant="outlined" style={{width: '100ch',}}
                multiline
                value={props.body1}
                />
            </div>
          </Fade>
        </Modal>
      </div>
    );
  }